.main-content__picture {
  width: 520px;
  height: 260px;
  display: block;

  @media (min-width: @tablet-width) {
    width: 100%;
    height: 256px;
  }

  @media (min-width: @tablet-width) {
    width: 100%;
    height: 256px;
  }

  &--order {
    @media (min-width: @tablet-width) {
      order: -1;
    }
  }
}

.main-content__picture-retina {
  @media (min-width: @tablet-width) {
    order: -1;
  }
}

.main-content__text {
  text-align: center;

  @media (min-width: @tablet-width) {
    min-height: 256px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    flex-wrap: wrap;
    align-items: center;
    justify-items: center;
  }

  &--background-1 {
    background-color: @darkblue;
  }
}

// Main-content__list
.main-content__list {
  counter-reset: li;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

//Main-content__item
.main-content__item {
  position: relative;
  counter-increment: li;

  &--color {
    color: @dark;
    background-color: @grey-50;
    text-align: center;

    @media (min-width: @tablet-width) {
      display: block;
    }
  }

  &--size {
    padding-top: 26px;
    padding-bottom: 24px;
    position: relative;

    @media (min-width: @tablet-width) {
      padding-top: 42px;
      padding-bottom: 63px;
    }

    &::before {
      position: absolute;
      content: "";
      width: 280px;
      height: 1px;
      background-color: @grey-170;
      top: 0;
      left: 18px;

      @media (min-width: @tablet-width) {
        display: none;
      }
    }
  }

  &--layout {
    @media (min-width: @tablet-width) {
      display: grid;
      grid-template-columns: 256px 1fr;
      grid-column-start: 1;
      grid-column-end: -1;
    }

    @media (min-width: @desktop-width) {
      display: grid;
      grid-template-columns: 400px 1fr;
    }
  }

  &--grid {
    @media (min-width: @tablet-width) {
      display: grid;
      grid-template-columns: 1fr 256px;
      grid-column-start: 1;
      grid-column-end: -1;
    }

    @media (min-width: @desktop-width) {
      grid-template-columns: 1fr 400px;
    }
  }
}

// Main-content__list-city
.main-content__list-city {
  margin-top: 9px;
  margin-bottom: 32px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  padding: 0 18px;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-start: 1;
    grid-column-end: -1;
    column-gap: 36px;
    flex-wrap: wrap;
    text-align: center;
    justify-items: center;
    align-items: baseline;
    margin: 0;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 20px;
  }
}

// main-content__item-city
.main-content__item-city {
  margin-top: 26px;
  padding-left: 52px;
  position: relative;

  &--position {
    @media (min-width: @tablet-width) {
      order: 2;
    }
  }

  @media (min-width: @tablet-width) {
    margin: 0;
    padding-left: 0;
    padding-top: 160px;
    padding-bottom: 60px;
    min-width: 185px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 0;
    min-width: 282px;
  }
}

.main-content__item-city::before {
  content: "";
  width: 40px;
  height: 39px;
  position: absolute;
  background-repeat: no-repeat;
  left: 0;
  top: -10px;

  @media (min-width: @tablet-width) {
    width: 76px;
    height: 75px;
    left: 72px;
    top: 60px;
  }

  @media (min-width: @desktop-width) {
    left: 102px;
  }
}

