@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../../../fonts/ptsans.woff2") format("woff2"), url("../../../fonts/ptsans.woff format") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../../../fonts/ptsansbold.woff2") format("woff2"), url("../../../fonts/ptsansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
::before,
::after {
  box-sizing: border-box;
}
.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
  color: #333333;
  text-transform: uppercase;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  background-color: #ffffff;
}
.page__wrapper {
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}
@media (min-width: 768px) {
  .page__wrapper {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page__wrapper {
    width: 1200px;
  }
}
.page-header {
  position: relative;
}
@media (min-width: 768px) {
  .page-header {
    flex-direction: row;
  }
}
.page-header__logo img {
  height: 84px;
  position: absolute;
  bottom: -27px;
  left: calc(50% - 51px);
  z-index: 1;
  /*@media (min-width: @desktop-width) {
    width: auto;
    max-width: 187px;
    height: 96px;
    top: 0;
    background-image: url("../img/logo/Vector.svg");
    background-position: -42px 0;
    background-repeat: no-repeat;
  }*/
}
@media (min-width: 1200px) {
  .page-header__logo img {
    width: 187px;
    height: auto;
    left: calc(50% - 92px);
    top: 0;
  }
}
.page-footer {
  text-align: center;
  color: #000000;
}
@media (min-width: 768px) {
  .page-footer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 6px;
    flex-wrap: wrap;
    align-items: center;
    justify-items: center;
  }
}
@media (min-width: 1200px) {
