.main-nav {
  background-color: #ffffff;
  min-height: 56px;
  color: #ffffff;
  position: relative;
  padding-bottom: 56px;
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #ffffff;
    color: #000000;
    margin-top: 0;
    padding-bottom: 0;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 23px;
  height: 22px;
  top: 0;
  right: 0;
  background-color: transparent;
  background-image: url("data:image/svg+xml,%3Csvg width='23' height='22' viewBox='0 0 23 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M22.9 0H0V4H22.9V0Z' fill='%2381B3D2'/%3E%3Cpath d='M22.9 9H0V13H22.9V9Z' fill='%2381B3D2'/%3E%3Cpath d='M22.9 18H0V22H22.9V18Z' fill='%2381B3D2'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  cursor: pointer;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle {
  top: 15px;
  z-index: 2;
}
.main-nav--opened .main-nav__toggle::after {
  position: absolute;
  content: "";
  width: 23px;
  height: 23px;
  top: 0;
  right: 0;
  cursor: pointer;
  background-image: url("data:image/svg+xml,%3Csvg width='23' height='23' viewBox='0 0 23 23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M22.2 2.8L19.4 0L11.1 8.3L2.8 0L0 2.8L8.3 11.1L0 19.4L2.8 22.2L11.1 13.9L19.4 22.2L22.2 19.4L13.9 11.1L22.2 2.8Z' fill='white'/%3E%3C/svg%3E ");
  background-position: 0 0;
  background-repeat: no-repeat;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  display: block;
}
.main-nav__toggle {
  position: absolute;
  width: 23px;
  height: 22px;
  top: 17px;
  right: 20px;
  bottom: 17px;
  left: auto;
  border: none;
  background-color: transparent;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  min-width: 320px;
  min-height: 223px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (min-width: 768px) {
  .main-nav__list {
    position: static;
    top: 0;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    min-height: 56px;
    box-shadow: none;
  }
}
@media (min-width: 1200px) {
