.page-header {
  position: relative;

  @media (min-width: @tablet-width) {
    flex-direction: row;
  }
}

.page-header__logo img {
  height: 84px;
  position: absolute;
  bottom: -27px;
  left: calc(50% - 51px);
  z-index: 1;

  @media (min-width: @desktop-width) {
    width: 187px;
    height: auto;
    left: calc(50% - 92px);
    top: 0;
  }

